@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&family=Roboto&display=swap);
/* App.css */
/* *body{background-color: #2f2f2f;} */
*{margin: 0;padding: 0; box-sizing: border-box;}
body{font-family: Roboto, sans-serif; background-color: #eff3f7; height: 100%;}

/* Movie.css */
.movies .movie{
    background-color: #fff;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3),
    0 -12px 36px -6px rgba(0, 0, 0, 0.025);
    /* box-shadow: x축offset y축offset blur spread color; 
    spread는 그림자의 확장, 양수는 확장, 음수는 축소 */
}

.movies .movie a{
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
}

.movie img{
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3),
    0 -12px 36px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .movie_year{
    margin: 0;
    font-weight: 300;
}

.movie .movie_title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #3b7ffc;
    font-weight: bold;
}

.movie .movie_genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}

.movie .movie_genre,
.movie .movie_year{
    margin-right: 10px;
    font-size: 14px;
}



/* Hoem.css */
.container{
    height: 100%;display: flex;justify-content: center;
}
.loader{
    width: 100vh; 
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}
.movies{
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

@media screen and (max-width:767px) { /* Mobile 크기 0~767 */
    .movies{
        grid-template-columns: 1fr;
        width: 100%;
    }
}
/* About.css */
.about_container{display: flex; flex-direction: column; justify-content: space-around;align-items: center;
    width: 100%;max-width: 400px;margin: 0 auto;margin-top: 100px;padding: 20px;border-radius: 5px;background-color: #fff;font-weight: 300; 
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),
    0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.about_container span:first-of-type{font-size: 20px;}
/* Navigation.css */
.nav{z-index: 1; position: fixed;left: 10px;top: 50px;
    background-color: #fff;padding: 10px 20px; border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),
    0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    display: flex;flex-direction: column;
}

.nav a{text-align: center;text-decoration: none;color: #0008fc; text-transform: uppercase;font-size: 12px;font-weight: 600;}

.nav a:not(:last-child){margin-bottom: 20px;}

@media screen and (max-width: 767px) {
    .nav{width: 100%;bottom: 0;left: auto;left: initial; top: auto; top: initial;}
    /* left:auto; top:auto; */
}
/* Detail.css */
.detail{
    display: grid;
    grid-template-columns: minmax(400px, 1fr) 2fr;
    grid-gap: 20px;

    background-color: white;
    margin: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(50, 50, 93, 0.3),
    0 -6px 16px -6px rgba(50, 50, 93, 0.025);
}

.detail img{
    position: relative;
    top: -50px;
    max-width: 400px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(50, 50, 93, 0.3),
    0 -12px 36px -8px rgba(50, 50, 93, 0.025);
}
.detail .detail_title,
.detail .detail_year{
    margin: 0;
    font-weight: 300;
}
.detail .detail_title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #3b7ffc;
    font-weight: bold;
}
.detail .detail_genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}
.detail_genres li,
.detail .detail_year{
    margin-right: 10px;
    font-size: 14px;
}

@media screen and (max-width:767px) {
    .detail{
        grid-template-columns: 1fr;
        width: calc(100% - 140px);
    }
}
